.search-module {
  position: relative; }

.search-results {
  position: absolute;
  width: 20rem;
  background-color: #eff1f2;
  border: none;
  border-radius: 0.2rem;
  color: black;
  z-index: 20;
  padding: 5px;
  text-align: left; }
  .search-results .search-result-items {
    padding: 0.25rem 0;
    border: 1px solid #777777; }
  .search-results .result-title {
    font-size: 1.5rem;
    font-weight: bold;
    cursor: pointer; }
  .search-results .result-location .loc {
    font-weight: bold; }



